.pagination__container {
    width: 100%;
    display: flex;
    padding: 0 150px;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}

.pagination__numbers {
    display: flex;
    margin: 0 50px;
    align-items: center;
}

.pagination__number {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #cfd5d7;
    transition: all ease .3s;
    color: #fff;
    box-shadow: 0 0 1px #444;
    cursor: pointer;
    &:hover, &.active {
        color: #333;
        background-color: #fff;
    }
    margin-right: 20px;
}
.pagination__arrow {
    font-size: 30px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #cfd5d7;
    transition: all ease .3s;
    &:hover {
        color: #444;
    }
}