@import '../../constants.scss';

.pharm-card {
    width: 300px;
    display: flex;
    flex-direction: column;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.3);
    border-radius: 4px;
    overflow: hidden;
    margin: 20px;
    transition : all ease .4s;
    cursor: pointer;
    &:hover {
        box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.2);
    }
    .pharm-card__img-container {
        width: 100%;
        height: 280px;
        .pharm-card__img {
            width: 100%;
            height: 100%;
        }
    }

    .pharm-card__container {
        display: flex;
        flex-direction: column;
    }

    .pharm-card__name {
        width: 100%;
        padding: 20px 0 20px 0;
        text-align: center;
        font-size: 14px;
        color: #fff;
        font-weight: 700;
        background-color: $green;
    }

    .pharm-card__description {
        max-width: 100%;
        display: flex;
        flex-direction: column;
        padding: 10px;
    }

    .pharm-card__description__list {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin: 10px 0 10px 0;
    }

    .pharm-card__description__item {
        text-align: right;
    }
}