html {
    min-height: 100%;
    height: 100%;
}

body {
    min-height: 100%;
    height: 100%;
    position: relative;
    box-sizing: border-box;
    background-color: #ecf0f1;
    
}

.footer {
    width: 100%;
    background-color: #24262B;
    color: #fff;
    margin-top: 50px;
    position: absolute;
    bottom: 0;
    left: 0;
}

.footer__top {
    display: flex;
    justify-content: space-around;
    &-desc {
        display: flex;
        flex-direction: column;
        margin-right: 50px;
    }
    &-title {
        font-size: 20px;
        text-transform: uppercase;
        margin-bottom: 15px;
        font-weight: 700;
        color: #fff;
    }
    &-text {
        font-size: 16px;
        line-height: 1.5;
        margin-bottom: 20px;
    }
}
.footer__top-social-title {
    margin-bottom: 20px;
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
}

.footer__top-social_icons {
    display: flex;
    img {
        margin-right: 10px;
        width: 30px;
    }
}

.footer__top-feedback {
    display: flex;
    flex-direction: column;
    max-width: 460px;
    width: 100%;
}

.feedback {
    &__title {
        font-size: 20px;
        margin-bottom: 20px;
        font-weight: 700;
        color: #fff;
        text-transform: uppercase;
    }
    &__container {
        display: flex;
        flex-direction: column;
    }
    &__top {
        display: flex;
        margin-bottom: 20px;
    }
    &__input {
        margin-right: 10px;
        padding: 5px;
        width: 100%;
        background-color: #32343B;
        &:last-child {
            margin-right: 0;
        }
        border: 1px solid #333;
    }
    &__textarea {
        resize: none;
        margin-bottom: 20px;
        padding: 5px;
        height: 100px;
        background-color: #32343B;
        border: 1px solid #333;
    }
    &__button {
        text-transform: uppercase;
        padding: 10px;
        background-color: #32cd32;
        border: none;
        max-width: 190px;
        align-self: flex-end;
        color: #fff;
        font-weight: 700;
        cursor: pointer;
    }
}

.footer-wrapper {
    max-width: 1600px;
    width: 100%;
    margin: 0 auto;
    padding: 30px;
}

.footer__top-desc {
    max-width: 500px;
}