.help-container {
    width: 100%;
    display: flex;
}

.help_description {
    width: 50%;
    background-color: rgb(28,28,28);
    padding: 100px;
    color: #fff;
}

.help_title {
    color: #fff;
    font-size: 34px;
    margin-bottom: 30px;
    font-weight: 700;
}

.help_form_container {
    width: 50%;
    padding: 100px;
    position: relative;
}

.help_form_form {
    background-color: rgb(44,44,44);
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;

}
.request_form_title {
    font-size: 34px;
    margin-bottom: 30px;
    font-weight: 700;
    color: rgb(224,192,116);
}

.help_form_form {
    display: flex;
    flex-direction: column;
    z-index: 10;
    position: relative;
}

.help_form_bg {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
filter: grayscale(.7)
}

.request_input {
    border: none;
    background-color: rgb(56,56,56);
    padding: 15px;
    border-radius: 10px;
    max-width: 500px;
    margin-bottom: 20px;
    width: 100%;
    &.textarea {
        height: 150px;
    }
}
.request_checkBox {
    display: flex;
    margin-bottom: 20px;
    .block {
        margin-right: 40px;
        color: #fff;
        input {
            margin-right: 10px;
        }
    }
}

.request_btn {
    padding: 20px;
    color: #fff;
    background-color: rgb(224,192,116);
    border: none;
    align-self: center;
    max-width: 250px;
    width: 100%;
    border-radius: 40px;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
}