.map {
  background-color: gold;
  width: 100%;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  & + div {
    position: relative; 
  }
  position: relative; 
  margin-bottom: 40px;
}

.map-container {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

}

body {
  padding-top: 60px;
}

.logo {
  color: '#000';
  font-size: 30px;
  font-weight: bold;
  position: absolute;
  top: 50px;
  left: 50px;
}

.ant-input {
  padding: 10px;
  font-size: 20px;
  height: auto;
}

.ant-btn {
  width: 100px;
  height: 50px;
  font-size: 20px;
  text-align: center;
}

.form-text {
  font-size: 20px;
}

.login-form-button-reg {
  width: 200px;
  margin-right: 20px;
}

.form-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}

.form-container {
  margin-top: 10%;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  display: flex;
}

.form-wrap {
  width: 400px;
}

.wrapper {
  max-width: 1600px;
  width: 100%;
  margin: 0 auto;
}