.carddesc {
    width: 70%;
    position: fixed;
    top: 0;
    left: -100%;
    height: 100%;
    background-color: #fff;
    color: #333;
    z-index: 20000;
    transition: all ease .7s;
    box-shadow: 0 0 10px #333;
    padding: 70px 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}


.carddesc.active {
    left: 0;
}

.carddesc__close {
    position: absolute;
    top: 30px;
    right: 30px;
    color: #333;
    font-size: 30px;
    cursor: pointer;
}

.carddesc__title {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 20px;
}

.carddesc__short-desc_top {
    margin-bottom: 40px;
    background-color: #32cd32;
    color: #fff;
    padding: 10px;
    box-shadow: 0 0 10px #333;
    border-radius: 5px;
}

.carddesc__workday {
    padding: 5px 10px;
}

.carddesc__title.main {
    color: #fff;
    margin-bottom: 10px;
    display: block;

}

.carddesc__short-desc_bot {
    box-shadow: 0 0 5px #333;
    padding: 10px;
    border-radius: 5px;
}

.carddesc__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;

}

.carddesc__top-img {
    max-width: 500px;
    max-height: 400px;
    margin-right: 50px;
    width: 100%;
}

.carddesc__phone {
    color: #fff;
    &:hover {
        color: #fff;
    }
}

.carddesc__address {
    font-size: 20px;
    font-weight: 700;
}


