@import '../../constants.scss';

.navItem {
    padding-bottom: 20px;
    position: relative;
    margin-right: 35px;
}

.navItem::after {
    content: '';
    width: 20px;
    height: 5px;
    background-color: $orange;
    position: absolute;
    bottom: 0;
    left: 0;
    transition: all ease .3s;
}

.navItem:hover::after {
    width: 100%;
}



.navItem:hover a {
    color: $orange !important;
}

.enter {
    padding-right: 40px;
    display: flex;
    align-items: center;
    margin-bottom: 6px;
    cursor: pointer;
    position: relative;
    svg {
        color: $orange;
        position: absolute;
        left: -40px;
        transition: all ease 1.5s;
    }
   &:hover {
       
        svg {
            left: 120%;
        }
   }
}

.enter__text {
    margin-left: 10;
    color:#333;
}