.wrapper.__forum {
    display: flex;
    justify-content: space-around;
    align-items: center;
   
}

.wrapper.__padding {
    padding:  0 50px;
}

.forum__top {
    width: 100%;
    padding: 10px 0; 
    background-color: #fff;
    margin-bottom: 30px;
}


.forum__top_category {
    padding: 10px;
    border-radius: none;
    border: none;
    background-color: #fff;
    height: 30px;
    width: 150px;
    font-size: 18px;
    font-weight: 700;
}

.forum__top_search {
    display: flex;
    align-items: center;
    border-radius: 10px;
    overflow: hidden;
}

.forum_search_field {
    padding: 15px;
    border: none;
    background-color: #F4F5F9;
    color: #999;
}


.forum_search_button {
    background-color: #697683;
    padding: 11px;
    cursor: pointer;
    img {
        max-width: 25px;
        display: flex;
        align-items: center;
        justify-content: center;

    }
}

.forum__main {
    display: flex;
    margin-top: 50px;
}

.forum__main_left {
    margin-right: 40px;
    display: flex;
    flex-direction: column;
    width: 65%;
}

.forum__new-message {
    display: flex;
    background-color: #fff;
    padding: 10px;
}

.forum__avatar {
    padding: 15px;
    position: relative;
    &::after {
        content: "";
        height: 1px;
        position: absolute;
        width: 90%;
        top: 15%;
        left: 50%;
        transform: translateX(-50%);
        background-color: #F4F5F9;
    }

    &._c {
        &::after {
            top: 60%;
       
        }
    }
    &-img {
        border-radius: 50%;
    }
}

.forum__new-message {
    &-main {
        display: flex;
        flex-direction: column;
        padding: 15px;
        width: 100%;
    }
    &-title {
        padding: 20px;
        width: 100%;
        background-color: #F4F5F9;
        color: #999;
        box-sizing: border-box;
        margin-bottom: 20px;
        border: none;
        border-radius: 5px;
    }

    &-select {
        width: 200px;
        height: 40px;
        margin-bottom: 20px;
    }

    &-textarea {
        resize: none;
        width: 100%;
        padding: 15px;
        background-color: #F4F5F9;
        color: #999;
        box-sizing: border-box;
        height: 200px;
        margin-bottom: 20px;
        border: none;
        border-radius: 5px;
    }

    &-button {
        text-transform: uppercase;
        padding: 20px;
        background-color: #32cd32;
        color: #fff;
        border-radius: 10px;
        align-self: flex-end;
        cursor: pointer;
    }
    margin-bottom: 20px;
}

.forum__main_sidebar {
    width: 20%;
}

.forum__main_sidebar-item {
    background-color: #fff;
    padding: 20px;

}

.forum_sidebar-title {
    font-size: 16px;
    font-weight: 400;
    position: relative;
    width: 100%;
    box-sizing: border-box;
    &::after {
        content: "";
        height: 1px;
        position: absolute;
        width: 140%;
        bottom: -40%;
        left: -20%;
        background-color: #F4F5F9;
    }
    margin-bottom: 20px;
}

.forum_sidebar-category {
    font-size: 14px;
    margin-bottom: 20px;
}


.forum__comments {
    display: flex;
    flex-direction: column;
}

.forum__comment {
    display: flex;
    padding: 20px;
    margin-bottom: 20px;
    &-main {
        display: flex;
        flex-direction: column;
        padding: 10px;
        width: 100%;
        box-sizing: border-box;
    }
    &-title {
        font-size: 20px;
        font-weight: 700;
        margin-bottom: 20px;
    }

    &-text {
        line-height: 1.5;
        margin-bottom: 20px;
        width: 100%;
    }
    &-date {
        color: #000;
        align-self: flex-end;
    }
    background-color: #fff;
}